<template>
  <div class="star">
    <div class="star-item" :class="starType">
      <span v-for="itemClass in itemClasses" :class="itemClass" class="star-item" ></span>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
const LENGTH = 5;
const CLS_ON = "on";
const CLS_HALF = "half";
const CLS_OFF = "off";
export default {
  props: {
    size: {
      type: Number
    },
    score: {
      type: Number
    }
  },
  computed: {
    starType() {
      return "star-" + this.size;
    },
    itemClasses() {
      let result = [];
      let score = Math.floor(this.score * 2) / 2;
      let hasDecimal = score % 1 !== 0;
      let integer = Math.floor(score);
      for (let i = 0; i < integer; i++) {
        result.push(CLS_ON);
      }
      if (hasDecimal) {
        result.push(CLS_HALF);
      }
      while (result.length < LENGTH) {
        result.push(CLS_OFF);
      }
      return result;
    }
  }
};
</script>
<style lang="less">
@import "../../common/stylus/mixin.less";

.star {
  font-size: 0px;

  .star-item {
    display: inline-block;
    background-repeat: no-repeat;

    &.star-48 {
      .star-item {
        width: 0.20rem;
        height: 0.20rem;
        margin-right: 0.10rem;
        background-size: 0.20rem 0.20rem;

        &:last-child {
          margin-right: 0;
        }

        &.on {
          .bg-image("star48_on");
        }

        &.half {
          .bg-image("star48_half");
        }

        &.off {
          .bg-image("star48_off");
        }
      }
    }

    &.star-36 {
      .star-item {
        width: 0.15rem;
        height: 0.15rem;
        margin-right: 0.03rem;
        background-size: 0.15rem 0.15rem;

        &:last-child {
          margin-right: 0;
        }

        &.on {
          .bg-image("star36_on");
        }

        &.half {
          .bg-image("star36_half");
        }

        &.off {
          .bg-image("star36_off");
        }
      }
    }

    &.star-24 {
      .star-item {
        width: 0.10rem;
        height: 0.10rem;
        margin-right: 0.03rem;
        background-size: 0.10rem 0.10rem;

        &:last-child {
          margin-right: 0;
        }

        &.on {
          .bg-image("star24_on");
        }

        &.half {
          .bg-image("star24_half");
        }

        &.off {
          .bg-image("star24_off");
        }
      }
    }
  }
}
</style>



